






































.Projects {
  h2 {
    font-weight: 500;
  }
  & {
    position: relative;
    display: inline-flex;
    max-width: 100vw;
  }
  > * {
    margin: 1.5rem 0;
    padding: 0 1.5rem;
  }
  >:first-child { margin-left:  0 }
  >:last-child  { margin-right: 0 }
  >:only-child  { margin-left:  0 ;
                  margin-right: 0 }
  &-list {
    position: sticky;
    top: 0;
    flex: 0 25vw;
    max-height: 100vh;
    white-space: nowrap;
    margin: 1.5rem 0;
    a {
      display: block;
      padding: 0.375rem .75rem;
    }
    @media (max-width: 835px) {
      &:not([open]) {
        display: none;
      }
    }
  }
  &-main {
  }
}
